.elements {
  margin: 0 auto;
  padding-top: 51px;
  max-width: 880px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px 17px;
}

@media screen and (max-width: 1023px) {
  .elements {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}

@media all and (max-width: 767px) {
  .elements {
    grid-template-columns: 1fr;
    padding-top: 36px;
  }
}
